/** @format */

import React, { useEffect, useRef, useState } from 'react';

export const papaimg =
	'data:image/jpeg;base64,';
const calculateDispayTime = (dateString: string): string => {
	const givenDate = new Date(dateString);
	const currentDate = new Date();
	if (givenDate.getFullYear() !== currentDate.getFullYear()) return 'Before this year';
	if (givenDate.getMonth() !== currentDate.getMonth()) return 'Earlier this year';
	if (currentDate.getDate() - givenDate.getDate() > 7) return 'Earlier this month';
	if (currentDate.getDate() - givenDate.getDate() > 1) return 'Earlier this Week';
	console.log(currentDate.getHours() - givenDate.getHours());
	if (currentDate.getHours() - givenDate.getHours() > 1) return 'Earlier today';
	return Math.floor(givenDate.getTime() - currentDate.getTime() / (1000 * 60)) + 'mins ago';
};

interface Item {
	link: string;
	description: string;
	imgUrl: string;
	createdDateTime: string;
}
interface Data {
	content: Item[];
	last: boolean;
	first: boolean;
	size: number;
	totalElements: number;
}
export const Home = () => {
	const [Data, setData] = useState<Data>();
	const currentItemSendingLink = useRef('');
	useEffect(() => {
		let isMounted = true;
		document.title = 'AlokMeds | Home';
		fetch(`${process.env.REACT_APP_SERVER_URL}/api/users/`, { credentials: 'include' })
			.then(res => {
				if (isMounted) return res.json();
			})
			.then((data: Data) => {
				if (isMounted) setData(data);
			});
		return () => {
			isMounted = false;
		};
	}, []);
	const addUserPublications: React.ReactEventHandler<HTMLAnchorElement> = async e => {
		const options: RequestInit = { method: 'POST', credentials: 'include' };
		await fetch(`${process.env.REACT_APP_SERVER_URL}/api/auth/user/users/${currentItemSendingLink.current}`, options);
	};
	return (
		<>
			<section className='bg-dark p-5  text-light text-center'>
				<div className='container d-flex justify-content-between'>
					<div>
						<h1 className='fw-light'>Welcome to AlokMeds</h1>
						<p className='lead'> Get all your doubts clear and also view my latest publications</p>
					</div>
					<div>
						<img className='img-fluid' src={papaimg} alt='' />
					</div>
				</div>
			</section>
			<div className='my-4 container flex-row d-flex'>
				{Data?.content?.map(publication => (
					<div key={publication.link} className='card bg-light mx-5'>
						<div className='card-body text-center py-0'>
							<img className='img-fluid rounded-circle' src={papaimg} alt='' />
							<h4 className='card-text text-dark'>{publication.description}</h4>
							<div className='d-flex justify-content-between align-items-center'>
								<div className='btn-group'>
									<a
										onClick={e => {
											currentItemSendingLink.current = publication.link;
											addUserPublications(e);
										}}
										href={publication.link}
										target='_blank'
										rel='noreferrer'
										className='btn btn-sm btn-outline-secondary'>
										View
									</a>
								</div>
								<small className='text-muted'>{calculateDispayTime(publication.createdDateTime)}</small>
							</div>
						</div>
					</div>
				))}
			</div>
		</>
	);
};
export default Home;
